export const calculateCreatedTime = (timeCreated) => {
  let periods = {
    "nm trc": 365 * 30 * 24 * 60 * 60 * 1000,
    "thng trc": 30 * 24 * 60 * 60 * 1000,
    "tun trc": 7 * 24 * 60 * 60 * 1000,
    "ngy trc": 24 * 60 * 60 * 1000,
    "gi trc": 60 * 60 * 1000,
    "pht trc": 60 * 1000,
  };

  let diff = Date.now() - +new Date(`${timeCreated}`);

  for (const key in periods) {
    if (diff >= periods[key]) {
      let result = Math.floor(diff / periods[key]);
      return `${result} ${result === 1 ? key : key}`;
    }
  }

  return "By gi";
};
